import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEllipsisVertical, faList, faCalendar, faClock, faPlus, faDownload, faSearch } from '@fortawesome/free-solid-svg-icons';
import { useEffect, useState } from 'react';
import { supabase } from '../SupabaseClient';
import CalendarView from './components/CalendarView';

function Events() {
    const [events, setEvents] = useState([]);
    const [search, setSearch] = useState("");
    const [showCalendar, setShowCalendar] = useState(false);

    useEffect(() => {
        async function fetchEvents() {
            const { data, error } = await supabase
                .from('eventi')
                .select('id, nome_evento, descrizione, data_evento, posti_totali, posti_disponibili');
            if (error) console.error(error);
            else setEvents(data);
        }
        fetchEvents();
    }, []);

    const formatMonthDay = (dateString) => {
        if (!dateString) return { month: '', day: '' };
        const date = new Date(dateString);
        const month = date.toLocaleString('default', { month: 'short' });
        const day = date.getDate();
        return { month, day };
    };

    const isOnSale = (event) => {
        const now = new Date();
        const eventDate = new Date(event.data_evento);
        const postiDisponibili = event.posti_disponibili;
        return postiDisponibili > 0 && eventDate >= now;
    };

    const filteredEvents = events.filter(event =>
        event.nome_evento.toLowerCase().includes(search.toLowerCase())
    );

    return (
        <>
            <div className="breadcrumb">
                <h1>Events</h1>
            </div>
            <div className="search-bar">
                <div className='search-bar-button-wrapper'>
                    <div className='search-input-wrapper'>
                        <FontAwesomeIcon icon={faSearch} />
                        <input
                            type="text"
                            placeholder="Search events"
                            value={search}
                            onChange={e => setSearch(e.target.value)}
                        />
                    </div>
                    <button className="button-list" onClick={() => setShowCalendar(false)}>
                        <FontAwesomeIcon icon={faList} />
                        <div className='button-label'>List</div>
                    </button>
                    <button className="button-calendar" onClick={() => setShowCalendar(true)}>
                        <FontAwesomeIcon icon={faCalendar} />
                        <div className='button-label'>Calendar</div>
                    </button>
                    <button className="button-upcoming">
                        <FontAwesomeIcon icon={faClock} />
                        <div className='button-label'>Upcoming events</div>
                    </button>
                </div>
                <button className="button-create">
                    <FontAwesomeIcon icon={faPlus} />
                    <div className='button-label'>Create event</div>
                </button>
            </div>

            {showCalendar ? (
                <CalendarView events={filteredEvents} />
            ) : (
                <div className="table-container">
                    <table className="event-table">
                        <thead>
                            <tr>
                                <th>Event</th>
                                <th>Sold</th>
                                <th>Gross</th>
                                <th>Status</th>
                                <th>Options</th>
                            </tr>
                        </thead>
                        <tbody>
                            {filteredEvents.map(event => {
                                const { month, day } = formatMonthDay(event.data_evento);
                                const onSale = isOnSale(event);
                                return (
                                    <tr key={event.id}>
                                        <td className="event-details-wrapper">
                                            <div className="event-date">
                                                <div className="month">{month}</div>
                                                <div className="day">{day}</div>
                                            </div>
                                            <div className="event-details">
                                                <div className="event-name">
                                                    {event.nome_evento}
                                                </div>
                                                <div className="event-date-time">
                                                    {event.data_evento}
                                                </div>
                                            </div>
                                        </td>
                                        <td>{event.posti_totali - event.posti_disponibili} / {event.posti_totali}</td>
                                        <td>0.00</td>
                                        <td>
                                            {onSale ? (
                                                <span className="status-on-sale">On Sale</span>
                                            ) : (
                                                <span className="status-sold-out">Sold Out</span>
                                            )}
                                        </td>
                                        <td className='icon-wrapper'><FontAwesomeIcon icon={faEllipsisVertical} style={{ height: '20px' }} /></td>
                                    </tr>
                                )
                            })}
                        </tbody>
                    </table>
                    <div className='csv-export'>
                        <FontAwesomeIcon icon={faDownload} style={{ height: '20px' }} />
                        <p>CSV export</p>
                    </div>
                </div>
            )}
        </>
    );
}
export default Events;