import React, { useEffect, useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faEllipsisVertical,
  faList,
  faCalendar,
  faClock,
  faPlus,
  faDownload,
  faSearch,
} from "@fortawesome/free-solid-svg-icons";
import { supabase } from "../SupabaseClient";

function Orders() {
  const [orders, setOrders] = useState([]);
  const [search, setSearch] = useState("");

  useEffect(() => {
    async function fetchOrders() {
      const { data, error } = await supabase
        .from("iscrizioni")
        .select(`
          id,
          evento_id,
          email,
          nome,
          numero_persone,
          nominativi,
          token_modifica,
          data_iscrizione,
          eventi (
            nome_evento
          )
        `);
      if (error) console.error(error);
      else setOrders(data);
    }
    fetchOrders();
  }, []);

  // Filtro real-time sul nome del cliente
  const filteredOrders = orders.filter(order =>
    order.nome.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <>
      <div className="breadcrumb">
        <h1>Orders</h1>
      </div>
      <div className="search-bar">
        <div className="search-bar-button-wrapper">
          <div className="search-input-wrapper">
            <FontAwesomeIcon icon={faSearch} />
            <input
              type="text"
              placeholder="Search customer"
              value={search}
              onChange={e => setSearch(e.target.value)}
            />
          </div>
          <button className="button-list">
            <FontAwesomeIcon icon={faList} />
            <div className="button-label">List</div>
          </button>
        </div>
      </div>

      <div className="table-container">
        <table className="event-table">
          <thead>
            <tr>
              <th>ID</th>
              <th>Evento</th>
              <th>Email</th>
              <th>Nome</th>
              <th>Numero Persone</th>
              <th>Nominativi</th>
              <th>Data Iscrizione</th>
              <th>Options</th>
            </tr>
          </thead>
          <tbody>
            {filteredOrders.map(order => (
              <tr key={order.id}>
                <td>{order.id}</td>
                <td>{order.eventi?.nome_evento || order.evento_id}</td>
                <td>{order.email}</td>
                <td>{order.nome}</td>
                <td>{order.numero_persone}</td>
                <td>{order.nominativi}</td>
                <td>{order.data_iscrizione ? new Date(order.data_iscrizione).toLocaleString() : ""}</td>
                <td className='icon-wrapper'>
                  <FontAwesomeIcon icon={faEllipsisVertical} style={{ height: '20px' }} />
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        <div className='csv-export'>
          <FontAwesomeIcon icon={faDownload} style={{ height: '20px' }} />
          <p>CSV export</p>
        </div>
      </div>
    </>
  );
}
export default Orders;