import React, { useState } from "react";

// Utility per ottenere il primo giorno del mese e il numero di giorni
function getMonthInfo(events, year, month) {
    // Primo giorno del mese (0 = domenica)
    const firstDay = new Date(year, month, 1).getDay();
    // Numero di giorni nel mese
    const daysInMonth = new Date(year, month + 1, 0).getDate();

    // Mappa eventi per giorno
    const eventsByDay = {};
    events.forEach(ev => {
        const date = new Date(ev.data_evento);
        if (date.getFullYear() === year && date.getMonth() === month) {
            const day = date.getDate();
            if (!eventsByDay[day]) eventsByDay[day] = [];
            eventsByDay[day].push(ev);
        }
    });

    return { firstDay, daysInMonth, eventsByDay };
}

const monthNames = [
    "Gennaio", "Febbraio", "Marzo", "Aprile", "Maggio", "Giugno",
    "Luglio", "Agosto", "Settembre", "Ottobre", "Novembre", "Dicembre"
];
const dayNames = ["Dom", "Lun", "Mar", "Mer", "Gio", "Ven", "Sab"];

const CalendarView = ({ events }) => {
    const today = new Date();
    const [currentYear, setCurrentYear] = useState(today.getFullYear());
    const [currentMonth, setCurrentMonth] = useState(today.getMonth());

    const { firstDay, daysInMonth, eventsByDay } = getMonthInfo(events, currentYear, currentMonth);

    // Navigazione mesi
    const goToPrevMonth = () => {
        setCurrentMonth(prev => {
            if (prev === 0) {
                setCurrentYear(y => y - 1);
                return 11;
            }
            return prev - 1;
        });
    };

    const goToNextMonth = () => {
        setCurrentMonth(prev => {
            if (prev === 11) {
                setCurrentYear(y => y + 1);
                return 0;
            }
            return prev + 1;
        });
    };

    // Costruisci le celle del calendario
    const cells = [];
    for (let i = 0; i < firstDay; i++) {
        cells.push(<td key={`empty-start-${i}`}></td>);
    }
    for (let day = 1; day <= daysInMonth; day++) {
        cells.push(
            <td key={day} className={eventsByDay[day] ? "calendar-has-event" : ""}>
                <div className="calendar-day-number">{day}</div>
                {eventsByDay[day] && (
                    <ul className="calendar-events-list">
                        {eventsByDay[day].map(ev => (
                            <li key={ev.id}>
                                <strong>{ev.nome_evento}</strong>
                                <div style={{ fontSize: "0.85em" }}>{ev.descrizione}</div>
                            </li>
                        ))}
                    </ul>
                )}
            </td>
        );
    }
    while (cells.length % 7 !== 0) {
        cells.push(<td key={`empty-end-${cells.length}`}></td>);
    }

    const rows = [];
    for (let i = 0; i < cells.length; i += 7) {
        rows.push(<tr key={i}>{cells.slice(i, i + 7)}</tr>);
    }

    return (
        <div className="calendar-view">
            <div style={{ display: "flex", alignItems: "center", justifyContent: "center", gap: "1em" }}>
                <button onClick={goToPrevMonth} aria-label="Mese precedente">&lt;</button>
                <h2 style={{ margin: 0 }}>
                    {monthNames[currentMonth]} {currentYear}
                </h2>
                <button onClick={goToNextMonth} aria-label="Mese successivo">&gt;</button>
            </div>
            <table className="calendar-table">
                <thead>
                    <tr>
                        {dayNames.map(day => (
                            <th key={day}>{day}</th>
                        ))}
                    </tr>
                </thead>
                <tbody>{rows}</tbody>
            </table>
        </div>
    );
};

export default CalendarView;