import React, { useState } from "react";

const SubscriptionForm = () => {
    const [numeroPersone, setNumeroPersone] = useState(1);
    const [nominativi, setNominativi] = useState([""]);

    const handleNumeroPersoneChange = (e) => {
        const value = parseInt(e.target.value, 10);
        setNumeroPersone(value);

        if (value > nominativi.length) {
            setNominativi([...nominativi, ...Array(value - nominativi.length).fill("")]);
        } else {
            setNominativi(nominativi.slice(0, value));
        }
    };

    const handleNominativoChange = (index, e) => {
        const newNominativi = [...nominativi];
        newNominativi[index] = e.target.value;
        setNominativi(newNominativi);
    };

    const handleIncrement = () => {
        setNumeroPersone(prev => {
            const newValue = prev + 1;
            setNominativi([...nominativi, ""]);
            return newValue;
        });
    };

    const handleDecrement = () => {
        setNumeroPersone(prev => {
            if (prev > 1) {
                setNominativi(nominativi.slice(0, prev - 1));
                return prev - 1;
            }
            return prev;
        });
    };

    const handleSubmit = async (event) => {
        event.preventDefault();
        const formData = new FormData(event.target);
        const data = Object.fromEntries(formData.entries());
        data.nominativi = nominativi;
    };

    return (
        <div className="subscription-card">
            <h4 className="event-date">Saturday, June 14</h4>
            <h1 className="event-title">Lecco Pride 2025 - Serata disco</h1>
            <form className="subscription-form" id="formIscrizione" onSubmit={handleSubmit}>
                <h3>Iscriviti all'Evento</h3>
                <div className="form-group-row">
                    <label className="form-label">
                        <input type="email" name="email" placeholder="email" required />
                    </label>
                    <label className="form-label" style={{ display: "flex", alignItems: "center", gap: "8px" }}>
                        Numero persone:
                        <button className="decrement-button" type="button" onClick={handleDecrement} style={{ marginLeft: "8px" }}>-</button>
                        <input
                            type="number"
                            name="numero_persone"
                            min="1"
                            value={numeroPersone}
                            onChange={handleNumeroPersoneChange}
                            required
                            style={{ width: "50px", textAlign: "center" }}
                        />
                        <button className="increment-button" type="button" onClick={handleIncrement}>+</button>
                    </label>
                </div>
                {nominativi.map((nominativo, idx) => (
                    <label key={idx} className="form-label">
                        Persona {idx + 1}:
                        <input
                            type="text"
                            value={nominativo}
                            onChange={(e) => handleNominativoChange(idx, e)}
                            placeholder="nome e cognome"
                            required
                        />
                    </label>
                ))}
                <button type="submit">Iscriviti</button>
            </form>
        </div>
    );
};

export default SubscriptionForm;